import React, { Component } from 'react';
import Slider from "react-slick";
import Strategy1 from '../assets/images/strategy-logo-1.png';
import Strategy2 from '../assets/images/strategy-logo-2.png';
import { Col, Container, Image, Row } from 'react-bootstrap';

export default function OurStragtey() {
   
    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 2,
        autoplay: true,
    autoplaySpeed: 2000,
        slidesToScroll: 1,

        responsive: [{
 
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              infinite: true
            }
       
          }, {
       
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              dots: true
            }
       
          }, {
       
            breakpoint: 300,
            settings: "unslick" // destroys slick
       
          }]


      };
    
        return (
            <section className='deafultPadding ourClients stragteySlider'>
            <Container fluid >
              
                
                <Slider {...settings}>
      <div>
            <div> <Image src={Strategy1} alt="" className='img-fulid'/> </div>
      </div>
      <div>
         <div> <Image src={Strategy2} alt=""  className='img-fulid'/> </div>
      </div>
      <div>
      <div> <Image src={Strategy1} alt=""  className='img-fulid'/> </div> 
      </div>
      <div>
      <div> <Image src={Strategy2} alt=""  className='img-fulid'/> </div>
      </div>
      <div>
      <div> <Image src={Strategy1} alt=""  className='img-fulid'/> </div>
      </div>
      <div>
      <div> <Image src={Strategy2} alt=""  className='img-fulid'/> </div>
      </div>
    </Slider>
               
            </Container>
            </section>
        );
    }



