import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import HeaderSticky from "../../Layout/headerSticky";

const  Contact = () => {
  useEffect(() => {
    AOS.init({ disable: 'mobile'});
  }, []);
  return (
    <>
      <HeaderSticky />
      <div className="contactuspage">
        <section className="">
          <div className="container">
            <div className="row ">
              <div className="col-md-6">
                <div className="">
                  <h1 data-aos="zoom-in">
                    <span>- LET'S TALK</span>
                    Tell us all about your project right here
                  </h1>
                </div>

                <div className="addressBlock">
                      <div className="addressListing">
                          <span>LOCATION</span>  
                          B-41, Nehru Nagar, Jaipur, India (302016)
                      </div>
                      <div className="addressListing">
                          <span>TELEPHONE</span>  
                          +91 7726-998823
                      </div>
                      <div className="addressListing">
                          <span>SEND US AN EMAIL AT</span>  
                          sales@lexicmedia.com
                      </div>
                </div>
              </div>
              <div className="col-md-6">
                <div className="contactUsSec">
                  <form>
                    <div className="input-group">
                      <label>Your Name</label>
                      <input
                        className="form-control"
                        type="text"
                        placeholder="Type Your Name"
                      />
                    </div>
                    <div className="input-group">
                      <label>Company Name</label>
                      <input
                        className="form-control"
                        type="text"
                        placeholder="Type here"
                      />
                    </div>
                    <div className="input-group">
                      <label>Email Address</label>
                      <input
                        className="form-control"
                        type="text"
                        placeholder="Type here"
                      />
                    </div>

                    <div className="input-group">
                      <label>Project Budget</label>
                      <div className="radioOuter">
                      <div className="radio-group">
                      <input type="radio" className="btn-check" name="options" id="option1" autocomplete="off" checked/>
                        <label className="btn btn-secondary" for="option1">
                        $25k - $50K
                        </label>
                        </div>
                        <div className="radio-group">
                        <input
                          type="radio"
                          className="btn-check"
                          name="options"
                          id="option2"
                          autocomplete="off"
                        />
                        <label className="btn btn-secondary" for="option2">
                        $50k - $100K
                        </label>
              </div>
            


                        <div className="radio-group">
                      <input type="radio" className="btn-check" name="options" id="option3" autocomplete="off" checked/>
                        <label className="btn btn-secondary" for="option3">
                        $100k - $300K
                        </label>
                        </div>
                        
                     
                    </div>
                    </div>
                    <div className="input-group">
                      <label>How did you hear about us?</label>
                      <input
                        className="form-control"
                        type="text"
                        placeholder="Type here"
                      />
                    </div>
                    <div className="input-group">
                      <label>Project Description</label>
                      <textarea
                        className="form-control"
                        type="text"
                        placeholder="Type here"
                      />
                    </div>

                    <div className="input-group">
                    <button type="submit" className="btnDeafult trasnprent">
                  <span className="text">Submit</span>
                  <span className="icon">
                    {" "}
                    <i className="zmdi zmdi-arrow-right"></i>
                  </span>
                </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}


export default Contact