import React from 'react';
import './IndustriesValue.css';
import { Col, Container, Row } from 'react-bootstrap';
import industriesBanner from '../../../assets/images/industries-img.png';
import { Collapse } from 'bootstrap';

export default function IndustriesServe() {
  return (
    <>
            <section className='IndustriesSec'>
                     <Container fluid> 
                        <Row>
                                <Col lg="5">
                                <h2 className='deafultHeading  '>
                            <span>- INDUSTRIES WE SERVE</span>
                            Fundamental presence in all core sectors
                                </h2>
                                <p>We dont aim for the big fame, rather we establish stable and truly reliable brands.</p>
                                
                                </Col>

                                <Col lg="7">
                                        <img src={industriesBanner} className="img-fluid" alt=""/>
                                </Col>
                        </Row>
                           

                     </Container>
            </section>
    </>
  );
}
