import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ArrowLong from '../assets/images/long-arrow.png';

import { Col, Container, Image, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';

class FullBlock extends Component {
   

    render() {
        return (
            <section className='FullBlockSec'>
                    <div className='halfCol'>
                            <div className='halfInner'>
                                   <div> <h3>Rebrand</h3>
                                    <p>Existing website or app</p></div>
                                    <Link to='' className='longArrow'> <Image src={ArrowLong} alt=""/> </Link>
                            </div>
                    </div>
                    <div className='halfCol'>
                    <div className='halfInner'>
                                   <div>
                                   <h3>New Idea</h3>
                                    <p>Existing website or app</p>
                                   </div>
                                   <Link to='' className='longArrow'> <Image src={ArrowLong} alt=""/> </Link>
                            </div>
                    </div>
            </section>
        );
    }
}


export default FullBlock;