import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import PortfolioImg from "../../assets/images/portfolio-banner.png";
import HeaderSticky from "../../Layout/headerSticky";
import PortfolioBanner from '../../components/portfolio.js';
import PortfolioBannerTwo from "../../components/portfolio-two";
import Testimonials from "../../components/testimonail";
import OurJourney from "../../components/our-journey";
import FullBlock from "../../components/full-block";
import Footer from "../../Layout/footer";
import { Image } from "react-bootstrap";

export default function Portfolio() {
  useEffect(() => {
   AOS.init({ disable: 'mobile'});
  }, []);
  return (
    <>
      <HeaderSticky />
      <div className="portfolioPage">
              <div className="protfolioBanner">
                    <div className="container">
                    <div className="row">
                        <div className="col-md-8"> <h1><span>- LET'S TALK</span>  Great design is people who is make it.</h1></div>
                    </div>

                    <div className="protfolioBannerImage">
                          <div className="imageOuter">
                          <Image className="img-fluid" src={PortfolioImg} alt=""/>
                          <div className="projectTitles"><h3>Wallet App</h3><ul><li>Web and Mobile Design </li><li>Content Strategy </li><li>CMS Integration</li></ul></div>
                          </div>
                          <div className="descPortfolio">
                              <p>"These guys are great. They really know their stuff, up to the minute technology and design. I can highly recommend them. Lovely people to work with."</p>
                              <div className="text-center">
                              <div className="userInfo">
                                <span className="iconplay"><i className="zmdi zmdi-play"></i></span>
                                <div className="name">Sean Rad</div>
                                <p>Founder at Uber</p>
                            </div>

                              </div>

                          </div>
                    </div>
                    </div>
              </div>

            <div className="moreProjects">
            <PortfolioBannerTwo/>
            <Testimonials/>
              <OurJourney/>
                <FullBlock/>
                <Footer/>
            </div>

      </div>
    </>
  );
}
