import React, { useState } from "react";
import Menu from "../assets/images/menu.svg";
import { Link } from "react-router-dom";
import { Image } from "react-bootstrap";
export default function HeaderSticky() {
  const [isOpen, setOpen] = useState(false);
  return (
    <>
      <header className="header stickyHeader">
        <div className="continer">
          <nav className="navbar bg-transprent" sticky="top">
            <div className="container-fluid">
              <Link className="navbar-brand" to="/home">
                <Image width={250} src="https://www.lexicmedia.com/wp-content/uploads/2019/10/lexicmedia_logo2_1.png" alt="lexicmedia" />
              </Link>
              {/* <a className="navbar-brand" href="#">
              </a> */}

              <div className="navbar-brand">
                <a
                  role="button"
                  className={`navbar-burger burger ${isOpen && "is-active"}`}
                  aria-label="menu"
                  aria-expanded="false"
                  onClick={() => setOpen(!isOpen)}
                >
                  <Image
                    src={Menu}
                    alt="Logo"
                    width="30"
                    height="24"
                    className="d-inline-block align-text-top"
                  />
                </a>
              </div>
              <div className={`navbar-menu ${isOpen && "is-active"}`}>
                <span
                  className={`close ${isOpen && "is-active"}`}
                  onClick={() => setOpen(!isOpen)}
                >
                  {" "}
                  <i className="zmdi zmdi-close"></i>{" "}
                </span>
                <div className="row">
                  <div className="col-5 col-md-6">
                    <div className="navbar-start">
                      <div className="title">- MENU</div>
                      <Link
                        className="navbar-item  nav-link"
                        to="/home"
                      >
                        Home
                      </Link>

                      <Link
                        className="navbar-item nav-link"
                        to="/portfolio"
                      >
                        Work
                      </Link>

                      <Link
                        className="navbar-item nav-link"
                        to="/about"
                      >
                        About
                      </Link>
                      <Link
                        className="navbar-item nav-link"
                        to="/services"
                      >
                        Services
                      </Link>
                      <Link
                        className="navbar-item nav-link"
                        to="/marketplace"
                      >
                        Tutorials
                      </Link>
                      <Link
                        className="navbar-item nav-link"
                        to="/contact"
                      >
                        Contacts
                      </Link>
                      <div className="workBtn">
                        <a href="" className="btnDeafult trasnprent">
                          <span className="text">Fabulous Work</span>
                          <span className="icon">
                            {" "}
                            <i className="zmdi zmdi-arrow-right"></i>
                          </span>
                        </a>
                      </div>
                    </div>
                  </div>
                  <div className="col-5 col-md-6">
                    <div className="headerSocail">
                      <div className="title">- SOCIAL</div>
                      <ul className="headerSocail">
                        <li>
                          <Link to="">LinkedIn</Link>
                        </li>
                        <li>
                          <Link to="">Dribbble </Link>
                        </li>
                        <li>
                          <Link to="">Instagram </Link>
                        </li>
                        <li>
                          <Link to="">Twitter</Link>
                        </li>
                      </ul>
                    </div>
                    <div className="headerSocail getinTouch">
                      <div className="title">- GET IN TOUCH</div>
                      <ul className="headerSocail">
                        <li>
                          <Link to="">sales@lexicmedia.com</Link>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </nav>
        </div>
      </header>
    </>
  );
}
