import React, { Component, useEffect } from 'react';

import AOS from 'aos';
import 'aos/dist/aos.css';
import PropTypes from 'prop-types';
import Porfolio1 from '../assets/images/portfolio-1.jpg';
import Porfolio2 from '../assets/images/portfolio-2.jpg';
import Porfolio3 from '../assets/images/portfolio-3.jpg';
import { Col, Container, Image, Row } from 'react-bootstrap';

class PortfolioBanner extends Component {
    componentDidMount() {
       AOS.init({ disable: 'mobile'});
    }

    render() {
        return (
            <section className='deafultPadding'>
            <Container className="portfolioBlock">
                    <Row className='align-items-lg-center'>
                        <Col md="8" lg="9">
                                <div className='heading'>
                                    <h2><span>- PORTFOLIO</span> Selected Work.</h2>
                                </div>
                        </Col>
                        <Col md="4" lg="3">
                                <div className='d-flex justify-content-md-end'>  <a href='' className='btnDeafult trasnprent blueBtn'><span className='text'>Fabulous Work</span>
                                                    <span className='icon'> <i className="zmdi zmdi-arrow-right"></i></span>
                                                    </a> 
                                                    </div>
                        </Col>
                    </Row>

                <div className='portfolioListingTwoCol'>
                        <div className='projectOuter' data-aos="fade-right"
     data-aos-offset="300"
     data-aos-easing="ease-in-sine">
                                <Image src= {Porfolio1} className="img-fluid" alt=""/>  
                                <div className='projectTitles'>
                                        <h3>Wallet App</h3>
                                        <ul>
                                            <li>Web and Mobile Design </li>
                                            <li>Content Strategy </li>
                                            <li>CMS Integration</li>
                                        </ul>
                                </div>
                        </div>
                        <div className='projectOuter' data-aos="fade-left"
     data-aos-offset="300"
     data-aos-easing="ease-in-sine">
                                <Image src= {Porfolio2}  className="img-fluid" alt=""/>  
                                <div className='projectTitles'>
                                        <h3>Frontier</h3>
                                        <ul>
                                            <li>Web and Mobile Design </li>
                                            <li>Content Strategy </li>
                                           
                                        </ul>
                                </div>
                        </div>
  </div>
  <div className='portfolioListingTwoCol'>
             <div className='projectOuter full' d data-aos="fade-up"
     data-aos-anchor-placement="center-bottom">
                                <Image src= {Porfolio3}  className="img-fluid" alt=""/>  
                                <div className='projectTitles'>
                                        <h3>Frontier</h3>
                                        <ul>
                                            <li>Web and Mobile Design </li>
                                            <li>Content Strategy </li>
                                           
                                        </ul>
                                </div>
                        </div>
       </div>
            </Container>
            </section>
        );
    }
}


export default PortfolioBanner;