import React, { Component } from 'react';
import Slider from "react-slick";
import Client1 from '../assets/images/client-logo-1.jpg';
import Client2 from '../assets/images/client-logo-2.jpg';
import Client3 from '../assets/images/client-logo-3.jpg';
import Client4 from '../assets/images/client-logo-4.jpg';
import { Col, Container, Image, Row } from 'react-bootstrap';

export default function OurClient() {
   
    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 5,
        autoplay: true,
    autoplaySpeed: 2000,
        slidesToScroll: 1,

        responsive: [{
 
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              infinite: true
            }
       
          }, {
       
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              dots: true
            }
       
          }, {
       
            breakpoint: 300,
            settings: "unslick" // destroys slick
       
          }]


      };
    
        return (
            <section className='deafultPadding ourClients'>
            <Container >
               <h2 className='text-center'>Our Clients love our creativity, passion and innovation</h2>   
            <Slider {...settings}>
      <div>
            <div> <Image src={Client1} alt=""/> </div>
      </div>
      <div>
         <div> <Image src={Client2} alt=""/> </div>
      </div>
      <div>
      <div> <Image src={Client3} alt=""/> </div> 
      </div>
      <div>
      <div> <Image src={Client4} alt=""/> </div>
      </div>
      <div>
      <div> <Image src={Client1} alt=""/> </div>
      </div>
      <div>
      <div> <Image src={Client2} alt=""/> </div>
      </div>
    </Slider>
            </Container>
            </section>
        );
    }



